    .overlay {
        position: absolute;
        bottom: 0;
        background: rgb(29, 29, 27);
        background: rgba(29, 29, 27, 0.5);
        width: 100%;
        height: 100%;
        transition: .5s ease;
        opacity: 0;
        text-align: center;
        padding: 20% 0;
        text-decoration: none;
    }
    
    .overlayTitle {
        color: #FAE6C5;
        font-size: 180px;
        font-family: 'Bebas Neue';
        font-weight: 900;
        line-height: 1.1em;
    }
    
    #locals:hover .overlay {
        opacity: 1;
    }
    
    .overlayTitle:hover {
        color: #F2C571;
    }
    
    .overlaySubtitle {
        color: #FAE6C5;
        font-family: 'Bebas Neue';
        font-size: 50px;
        margin-top: -95px;
        text-transform: uppercase;
        font-weight: 300;
        line-height: 4em;
    }
    /* MOBILE */
    
    .overlayMobile {
        position: absolute;
        bottom: 0;
        background: rgb(29, 29, 27);
        background: rgba(29, 29, 27, 0.5);
        width: 100%;
        height: 100%;
        transition: .5s ease;
        opacity: 1;
        text-align: center;
        padding: 20% 0;
        text-decoration: none;
    }
    
    .overlayTitleMobile {
        color: #FAE6C5;
        font-size: 180px;
        font-family: 'Bebas Neue';
        font-weight: 900;
        line-height: 1.1em;
    }
    
    @media screen and (max-width: 500px) {
        .overlayTitleMobile {
            font-size: 100px;
            vertical-align: center;
            margin-top: 200px;
        }
        .overlayTitle,
        .overlaySubtitle {
            display: none;
        }
    }